.player-status {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 10px;
}

.player-status.inactive {
  opacity: 0.5;
}

.player-status .name {
  font-size: 18px;
  margin-right: 10px;
  word-break: break-word;
}

.player-status .points {
  font-size: 12px;
}

.player-status .water-level {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-right: 10px;
  padding: 2px;
  border: 1px solid black;
}

.player-status .water-level.emphased {
  border-width: 2px;
  border-color: red;
}

.player-status .water-level .level {
  font-size: 15px;
  font-weight: bold;
}
.player-status .water-level img {
  width: 20px;
  height: 20px;
}
.player-status .floats {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.player-status .float {
  width: 20px;
  height: 30px;
  margin: 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.player-status .float img {
  width: 15px;
  height: 15px;
}

.player-status .float:not(:first-child) {
  margin-left: 2px;
}

.player-status .float.red {
  background: red;
}

.player-status .float.blue {
  background: blue;
}


