.hand {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.hand.inactive {
  opacity: 0.8;
}

.hand li {
  margin: 2px;
  padding: 0;
  width: 40px;
  height: 60px;
  border: 1px solid black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: darkblue;
  cursor: pointer;
}

.hand.inactive li {
  cursor: not-allowed;
}

.hand li.selected {
  border-width: 3px;
  border-color: red;
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
}


